<template>
  <div class="about">
    <input type="text" v-model="keyword" placeholder="カクテル名など" class="w-4/5 bg-black bg-opacity-50 outline-none">
    <br>
    <button v-on:click="show" class="button m-3 text-base border-white border py-2 px-4">注文</button>
          <modal name="modal-test" :draggable="false" :resizable="false" :scrollable="true" width="85%" height="auto">
        <div v-for="(cocktail,key) in cocktailSerch" :key="key" class="my-3 border-b-2">
          <small class="text-black">{{cocktail.cocktail_digest}}</small><br>
          <u class="text-xl text-black">{{cocktail.cocktail_name}}</u>
          <div class="flex justify-between mx-3">
            <div class="bg-red-900 rounded-md px-2">Base</div><div class="text-black">{{cocktail.base_name}}</div>
            <div class="bg-red-900 rounded-md px-2">Taste</div><div class="text-black">{{cocktail.taste_name}}</div>
          </div>
        <div class="flex justify-between mx-3">
          <div class="bg-red-900 rounded-md px-2">Style</div><div class="text-black">{{cocktail.style_name}}</div>
          <div class="bg-red-900 rounded-md px-2">Alc.</div><div class="text-black">{{cocktail.alcohol}}%</div>
        </div>
        <h1 class="text-black text-left m-2">{{cocktail.cocktail_desc}}</h1>
        </div>
      </modal>
  </div>
</template>

<script>
import axios from 'axios'

export default{
  name: 'About',
  data() {
    return{
      cocktails: [],
      keyword:'',
    }
  },
  computed: {
    cocktailSerch: function() {
      var cocktail_serch = [];
    for(var i in this.cocktail_serch) {
        var cocktails = this.cocktail_serch[i];
        if(cocktails.cocktail_name.indexOf(this.keyword) !== -1) {
            cocktail_serch.push(cocktails);
        }
    }
    return cocktail_serch;


    }
    },
  methods: {
    show() {
      this.$modal.show('modal-test');
    },
    hide() {
      this.$modal.hide('modal-test');
      }
    },
        created() {
      axios.get(
        "https://cocktail-f.com/api/v1/cocktails?limit=100"
        )
    .then(response => {
      console.log(response);
      this.cocktails = response.data.cocktails;
      })
    },
  }
</script>